body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --mainBlue: rgb(12, 51, 182);
  --lightBlue: rgb(92, 84, 202);
  --mainWhite: #ffffff;
  --mainDark: rgb(109, 113, 131);
  --mainYellow: rgb(0, 225, 255);
  --mainAccent: pink;
}

body {
  /* font-family: 'Montserrat', sans-serif !important; */
  color: rgb(109, 113, 131);
  color: var(--mainDark);
  background: #ffffff !important;
  background: var(--mainWhite) !important;
}

.text-title {
  /* font-family: 'Space Mono', monospace !important; */
}

.text-blue {
  color: rgb(109, 113, 131);
  color: var(--mainDark)
}
.text-bright {
  color: rgb(109, 113, 131) !important;
  color: var(--mainDark) !important;
}

.br-primary {
  background: rgb(109, 113, 131);
  background: var(--mainDark);
}

.global-background {
  background-image: url(https://res.cloudinary.com/maiik/image/upload/v1549624607/Xsolla/HomePage_Hero_Illustration_1440_oabqmk.jpg);
  z-index: 0;
  background-size: cover;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.global-background-tint {
  background-color: #011627;
  opacity: 0.8;
  z-index: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.inventory-item {
    position: relative;
    transition: opacity 150ms ease-out, -webkit-transform 350ms ease-out;
    transition: transform 350ms ease-out, opacity 150ms ease-out;
    transition: transform 350ms ease-out, opacity 150ms ease-out, -webkit-transform 350ms ease-out;
}

.inventory-item.initialFlow1 {
    opacity: 0;
    -webkit-transform: translateY(20px) scale(1);
            transform: translateY(20px) scale(1);
}

.inventory-item-card {
    color: #D6E0E7;
    display: flex;
    flex-direction: column;
    max-width: 300px;
    min-width: 300px;
    height: 300px;
    margin: 16px;
    flex-shrink: 0;
    flex-grow: 1;
    flex-base: 50%;
    background: transparent;
    transition: box-shadow 0.3s ease-in-out;
    border-radius: 8px;
    padding: 8px;
}

.inventory-item-card:hover {
    box-shadow: 0 5px 12px 0px rgba(0,0,0,0.2);
}

.inventory-item-card-media {
    display: block;
    margin-top: 8px;
    min-height: 120px;
    flex-grow: 1;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.inventory-item-card-content {
    min-height: 66px;
    overflow: hidden;
    flex-grow: 0;
    padding: 16px;
    padding-bottom: 0;
    margin-bottom: 8px;
}

.inventory-item-card-content-title {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-weight: 700;
}

.inventory-item-card-content-description {
    display: block;
    display: -webkit-box;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 400;
}

.inventory-item-card-actions {
    display: flex;
    align-items: center;
    border-top: 1px solid rgba(68.808, 73.376, 86.135, 0.4);
    padding: 8px 0 0 0;
    margin: 0 16px 0 16px;
}

.inventory-item-card-actions-quantity {
    flex-grow: 1;
    font-size: 16px;
    font-weight: 600;
    color: #FF005B;
}
.inventory-list {
    position: relative;
    z-index: 4;
    background-color: transparent;
    color: #D6E0E7;
    display: flex;
    flex-wrap: wrap;
    place-content: center;
    margin-top: 70px;
}

.navbar-links {
    display: flex;
    padding-left: 28px;
    height: 100%;
}

.navbar-links a:hover {
    border: none;
    text-decoration: none;
}

.navbar-link {
    display: flex;
    align-items: center;
    min-height: 56px;
    text-transform: uppercase;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.4rem;
    margin: 0 1em 0 0;
    height: 100%;
    color: #d6e0e7;
    border-bottom: 2px solid transparent;
}

.navbar-link:hover {
    transition: 0.1s ease-out;
    opacity: 0.8;
    border-bottom: 2px solid rgba(214, 224, 231, 0.2);
}

.navbar-link_active > .navbar-link {
    color: #ff005b;
    border-bottom: 2px solid #ff005b;
}

.navbar-cart {
    background: #ff005b;
    border-radius: 4px;
    margin: 1em;
    padding: 0.24em 0.5em;
    position: relative;
    text-transform: uppercase;
    font-family: Space Mono;
    font-size: 0.8em;
    color: #d6e0e7;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    text-decoration: none;
}

.navbar-cart:hover {
    text-decoration: none;
}

.navbar-logout {
    display: flex;
    flex-direction: row;
    font-size: 0.8rem;
    line-height: 2em;
    flex-direction: column;
    justify-content: stretch;
    font-family: Roboto;
    color: #ff005b;
    border: 1px solid #ff005b;
    border-radius: 4px;
    padding: 0 0.6rem;
    cursor: pointer;
}
